@tailwind base;

@tailwind components;

@tailwind utilities;

@layer components {

    /*reuseable tailwind classes*/

    .navbar-icon {
        @apply relative flex items-center justify-center
               h-12 w-12 my-2 mx-auto shadow-lg
               bg-gray-800 text-green-500
               hover:bg-green-500 hover:text-white
               rounded-3xl hover:rounded-xl
               transition-all duration-300 ease-linear
               cursor-pointer;
    }

    .navbar-tooltip {
        @apply absolute w-auto p-2 m-2 min-w-max left-14
               rounded-md shadow-md
               text-white bg-gray-900
               text-xs font-bold
               transition-all duration-100 scale-0 origin-left;
    }

    .input-field{
        @apply border-black border-2 rounded-lg px-2 my-3 placeholder:text-gray-700;
    }

    .button-shadow {
        @apply shadow-black shadow-lg hover:shadow-none
    }
    
    .button-green {
        @apply rounded-lg bg-green-500 text-white 
               pt-1 pb-1 
               w-28 text-center
               hover:bg-green-800
               hover:cursor-pointer
               shadow-black shadow-md hover:shadow-none
    }

    .heading {
        @apply bg-green-500
                w-full ml-16 text-center 
               text-white pt-8 pb-8
               gothic
               font-extrabold
               text-5xl
               mb-6
    }

    .gothic {
            font-family: 'Didact Gothic', sans-serif; 
    }

    .input-field {
        @apply border-black border-2
               w-[50%]
               h-12 
               rounded-3xl 
               px-2 pl-4 pr-4
               shadow-black
               shadow-sm
               gothic
               placeholder:text-gray-700

   }
   .dob-input {
    
   }
    
}


::-webkit-scrollbar {
    z-index: 100;
    background-color: transparent;
}

::-webkit-scrollbar-track {
    min-height: screen;
    z-index: 100;
    background-color: rgba(14, 14, 14, 0.946);   
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    z-index: 1000;
    background-color: rgb(84, 84, 84);
}

html {
    overflow-x: hidden;
    max-height: screen;
}

